import React from "react";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import MenuItem from '@material-ui/core/MenuItem';
import AddIcon from '@material-ui/icons/Add';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    textField: {
        marginLeft: 16,
        marginRight: 16,
        width: 200,
      },
  });

// Dialog
class FormDialog extends React.Component {
  state = {
    open: false,
    task: "",
    type: "Feature",
    reach: 0,
    impact: 0,        
    confidence: 0,
    effort: 0,
    score: 0
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleChange = name => event => {
    event.preventDefault();
    let val;
    if(event.target.value) {
      val = event.target.value;
    }
    else {
      this.setState({score: "Not available"});
    }
    this.setState({
      [name]: val
    }, 
    // Calculate state after value has been set
    () => {
      this.calculateRICEScore()
    })
  };

  handleAddTask = () => {
    this.setState({ open: false });
    this.props.addNewTask(this.state.task, this.state.type, this.state.reach, this.state.impact,this.state.confidence,this.state.effort, this.state.score)
  };

  calculateRICEScore = () => {
    let reach  = this.state.reach,
        impact = this.state.impact, 
        confidence = this.state.confidence, 
        effort = this.state.effort
    if(reach && impact && confidence && effort ) {
      console.log("reach: " + reach + " impact: " + impact + " confidence: " + confidence + " effort: " + effort)
      //let score = null;
      let score = ((reach * impact * confidence) / effort);
      //score = score + 0.000
      this.setState({
        score: score
      });
    } 
    /*else {
      this.setState({
        score: 0
      });
    }*/
    
  }

  render() {
    const rangesTypes = [
      {
        value: 'Epic',
        label: 'Epic',
      },
      {
        value: 'Feature',
        label: 'Feature',
      },
      {
        value: 'User story',
        label: 'User story',
      },
    ];
    const rangesImpact = [
      {
        value: 0.25,
        label: 'Minimal',
      },
      {
        value: 0.50,
        label: 'Low',
      },
      {
        value: 1,
        label: 'Medium',
      },
      {
        value: 2,
        label: 'High',
      },
      {
        value: 3,
        label: 'Massive',
      },
    ];
    const rangesConfidence = [
      {
        value: 0.10,
        label: '10%',
      },
      {
        value: 0.25,
        label: '25%',
      },
      {
        value: 0.50,
        label: '50%',
      },
      {
        value: 0.75,
        label: '75%',
      },
      {
        value: 1,
        label: '100%',
      },
    ];

    return (
      <span>
      <Button variant="contained" title="Add task" color="primary"
        aria-label="Add task" className="add-button" onClick={this.handleClickOpen} >
        <AddIcon />
        Add new task
      </Button>
        <Dialog
          className="dialog-addTask"
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title" className="dialog-addTask_title">Add Task</DialogTitle>
          <DialogContent className="dialog-addTask_content">
            <DialogContentText>
              To add a new task to the list, add numbers for each section. 
            </DialogContentText>
            <span className="input-field_type">
        <TextField
          select
          className="input-field"
          variant="outlined"
          label="Type"
          value={this.state.type}
          onChange={this.handleChange('type')}
        >
          {rangesTypes.map(option => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
        </span>
            <TextField
          multiline
          variant="outlined"
          rows="2"
          id="standard-name"
          label="Task"
          className="input-field input-field_text"
          placeholder="Task description"
          value={this.state.task}
          onChange={this.handleChange('task')}
        />
            <TextField
          id="reach-number"
          label="Reach"
          className="input-field"
          variant="outlined"
          value={this.state.reach}
          onChange={this.handleChange('reach')}
          type="number"
        />
            <TextField
          select
          className="input-field"
          variant="outlined"
          label="Impact"
          value={this.state.impact}
          onChange={this.handleChange('impact')}
        >
          {rangesImpact.map(option => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
            <TextField
            select
          id="confidence-number"
          label="Confidence"
          className="input-field"
          variant="outlined"
          value={this.state.confidence}
          onChange={this.handleChange('confidence')}
          type="number"
        >
        {rangesConfidence.map(option => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
            <TextField
          id="effort-number"
          label="Effort"
          className="input-field"
          variant="outlined"
          value={this.state.effort}
          onChange={this.handleChange('effort')}
          type="number"
        />
        <Typography variant="h6" className="dialog-score">
        Score: {this.state.score}
      </Typography>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="secondary">
              Cancel
            </Button>
            <Button onClick={this.handleAddTask} color="primary" variant="contained">
              Add new task
            </Button>
          </DialogActions>
        </Dialog>
      </span>
    );
  }
}
export default withStyles(styles)(FormDialog);